import Vue from "vue";
import Router from "vue-router";
import Index from "@/views/Index";
import Login from "@/views/Login";
import Check from "@/views/Check";
import Dashboard from "@/components/Dashboard";
import Task from "@/components/Task";
import Label from "@/components/Label";
import VideoLabel from "@/components/VideoLabel";
import LabelsetVideo from "@/components/LabelsetVideo";
import User from "@/components/User";
import Labelset from "@/components/Labelset";
import Partition from "@/components/Partition";
import List from "@/components/List";
import { NAME } from "@/models/Service";
//import Dataset from "@/components/Dataset";
Vue.use(Router);
export default new Router({
  routes: [
    {
      path: "/",
      redirect: "/dashboard"
    },
    {
      path: "/",
      name: "Index",
      component: Index,
      props: true,
      children: [
        {
          path: "dashboard",
          name: "Dashboard",
          component: Dashboard,
          props: true,
          meta: {
            title: " - " + NAME
          }
        },
        {
          path: "task/:id/:taskName/:type",
          name: "Task",
          component: Task,
          props: true,
          meta: {
            title: " - " + NAME
          }
        },
        {
          path: "user",
          name: "User",
          component: User,
          props: true,
          meta: {
            title: " - " + NAME
          }
        },
        {
          path: "partition",
          name: "Partition",
          component: Partition,
          props: true,
          meta: {
            title: " - " + NAME
          }
        },
        {
          path: "list",
          name: "List",
          component: List,
          props: true,
          meta: {
            title: " - " + NAME
          }
        },
        /*{
          path: "dataset",
          name: "Dataset",
          component: Dataset,
          props: true
        },*/
        {
          path: "label",
          name: "Labelset",
          component: Labelset,
          props: true,
          meta: {
            title: " - " + NAME
          }
        },
        {
          path: "videolabel",
          name: "LabelsetVideo",
          component: LabelsetVideo,
          props: true,
          meta: {
            title: " - " + NAME
          }
        }
      ]
    },
    {
      path: "/img/:id/:tid/:type/:taskName/:imagePage",
      name: "Label",
      component: Label,
      props: true,
      meta: {
        title: " - " + NAME
      }
    },
    {
      path: "/video/:id/:tid/:taskName/:imagePage",
      name: "VideoLabel",
      component: VideoLabel,
      props: true,
      meta: {
        title: " - " + NAME
      }
    },
    {
      path: "/login/:path",
      name: "Login",
      component: Login,
      props: true,
      meta: {
        title: " - " + NAME
      }
    },
    {
      path: "/check/:path",
      name: "Check",
      component: Check,
      props: true,
      meta: {
        title: "Loading..."
      }
    }
  ]
});



