<template>
  <div>
    <el-button icon="el-icon-plus" @click="editUser" type="primary"></el-button>
    <el-button icon="el-icon-refresh-left" @click="showList"></el-button>

    <el-table border :data="taskData" stripe style="width: 100%;margin-top:10px;" v-loading="loading">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="table-expand">
            <el-form-item label="">
              <template v-if="props.row.users != null && props.row.users.length != 0">
                <span class="person" v-for="item in props.row.users" :key="item.userId">
                  {{ item.username }}({{ item.labeled }}/{{ props.row.imageNumber }})
                </span>
              </template>
              <span v-else></span>
            </el-form-item>
            <el-form-item label="">
              <template v-if="props.row.reviewers != null && props.row.reviewers.length != 0">
                <span class="person" v-for="item in props.row.reviewers" :key="item">
                  {{ item }}
                </span>
              </template>
              <span v-else></span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column
        :filters="[
          { text: '', value: '1' },
          { text: '', value: '2' },
          { text: '', value: '3' },
          { text: '', value: '4' },
          { text: '', value: '5' }
        ]"
        :filter-method="filterTag"
        filter-placement="bottom-end"
        prop="authorities"
        label=""
        width="180"
      >
        <template slot-scope="scope">
          {{ getTypeName(scope.row.taskType) }}
        </template>
      </el-table-column>
      <el-table-column prop="taskName" min-width="100" label=""></el-table-column>
      <el-table-column label="(/)">
        <template slot-scope="scope">
          {{ scope.row.finish }}/{{ scope.row.imageNumber }}
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="" :width="account.group == 3 ? 220 : 80">
        <template slot-scope="scope">
          <el-button @click="enterTask(scope.row)" type="primary" size="mini"></el-button>
          <el-button v-if="account.group == 3" @click="downloadTask(scope.row)" size="mini"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-dialog
      :title="userSelect.type == 'user' ? '' : ''"
      :visible.sync="userSelect.dialogVisible"
      @open="openUserDialog"
      width="400px"
    >
      <el-table ref="table" :data="taskData" style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column prop="join" type="selection" width="55"></el-table-column>
        <el-table-column label="" prop="taskName"></el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button @click="userSelect.dialogVisible = false"> </el-button>
        <el-button type="primary" @click="downloadMultiTask"> </el-button>
      </span>
    </el-dialog>

  </div>
</template>
<script>
import { getTaskListN as getList, getDataBlob } from "@/models/Task.js";
export default {
  name: "list",
  data() {
    return {
      taskData: [],
      account: this.global.account,
      loading: false,
      userSelect: {
        dialogVisible: false,
        tableData: [],
        tableSelect: [],
        type: "user",
        taskItem: [],
        loading: false
      },
      page: { now: 1, total: 1 }
    };
  },
  methods: {
    downloadMultiTask(){
      console.log(this.userSelect.tableSelect[1])
      console.log(this.userSelect.tableSelect.length)
      for(var i=0;i<this.userSelect.tableSelect.length;i++){
        this .downloadTask(this.userSelect.tableSelect[i])
      }

    },
    handleSelectionChange(val) {
      this.userSelect.tableSelect = val;
    },
    editUser(item) {
      [this.userSelect.dialogVisible, this.userSelect.type, this.userSelect.taskItem] = [true, "user", item];
    },
    showList() {
      if (this.account.group == 0) {
        return false;
      }
      this.loading = true;
      getList(this.account)
        .then(data => {
          this.taskData = data.data.taskList ? data.data.taskList : data.data;
        })
        .catch(data => {
          console.error(data);
          this.$message({ type: "error", message: "" });
        })
        .then(() => {
          this.loading = false;
        });
    },
    enterTask(item) {
      this.$router.push({ path: `/task/${item.taskId}/${item.taskName}/${item.taskType}` });
    },
    downloadTask(item) {
      if (this.account.group != 3) {
        this.$message({ type: "error", message: "" });
        return;
      }
      this.$message({ type: "info", message: "..." });
      getDataBlob(item.taskId)
        .then(d => {
          let date = new Date();
          let month = date.getMonth() + 1 + "";
          let day = date.getDay() + 1 + "";
          let hours = date.getHours() + "";
          let seconds = date.getSeconds() + "";
          let minutes = date.getMinutes() + "";
          month = month.length == 1 ? "0" + month : month;
          day = day.length == 1 ? "0" + day : day;
          hours = hours.length == 1 ? "0" + hours : hours;
          minutes = minutes.length == 1 ? "0" + minutes : minutes;
          seconds = seconds.length == 1 ? "0" + seconds : seconds;
          //prettier-ignore
          let filename = `${item.taskName}_${date.getFullYear()}-${month}-${day}_${hours}-${minutes}-${seconds}`;
          let blob = d.blob;
          let eleLink = document.createElement("a");
          eleLink.download = filename;
          eleLink.style.display = "none";
          eleLink.href = URL.createObjectURL(blob);
          document.body.appendChild(eleLink);
          eleLink.click();
          document.body.removeChild(eleLink);
        })
        .catch(data => {
          this.$message({ type: "error", message: `: ${data.message}(${data.code})` });
        });
    },
    getTypeName(type) {
      switch (type) {
        case 1:
          return "";
        case 2:
          return "";
        case 3:
          return "";
        case 4:
          return "";
        case 5:
          return "";
        default:
          return "";
      }
    },
    filterTag(val, row) {
      return row.taskType == val;
    },
    pageChange(p) {
      this.showList();
    }
  },
  mounted() {
    this.showList();
  }
};
</script>

<style>
.table-expand {
  font-size: 0;
}
.table-expand label {
  width: 90px;
  color: #99a9bf;
}
.table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
.el-form-item .person {
  margin-right: 5px;
}
</style>



