<template>
  <div>
    <template v-if="account.group == 3">

      <div class="dashboard" v-loading="loading">
        <div class="dashboard-panel panel-number">

          <div class="panel-header">
            
          </div>
          <div class="panel-content">
            {{ taskCount }}
          </div>
        </div>
        <div class="dashboard-panel panel-number">
          <div class="panel-header">
            
          </div>
          <div class="panel-content">
            {{ userCount }}
          </div>
        </div>
        <div class="dashboard-panel panel-number">
          <div class="panel-header">
            
          </div>
          <div class="panel-content">
            {{ reviewerCount }}
          </div>
        </div>
      </div>
    </template>
    <div class="">
      <h3 class="section-title"></h3>
      <p>
        2020-2-20 11:30pm  
      </p>
    </div>
    <div class="guide">
      <h3 class="section-title"></h3>
      <p>
        <b></b><br/>
        <b></b><i class="iconfont icon-nextweek icon"></i><br />
        <br/><br/>
        <b>/W</b><i class="iconfont icon-yidong icon"></i><br />
        <b>Q</b><i class="iconfont icon-duobianxing icon"></i
        ><br />/<br />
        <b>R</b><i class="iconfont icon-kuangxuanquyu icon"></i><br />
        <b>E</b><i class="iconfont icon-sheji icon"></i
        ><br />
        <b>D</b><i class="iconfont icon-shanchu icon"></i><br />
        <b>Z</b><i class="iconfont icon-fangda icon"></i><br />
        <b>C</b><i class="iconfont icon-suoxiao icon"></i><br />
        <b>Ctrl+S</b><i class="iconfont icon-baocun icon"></i><br />
        <b>Ctrl+C</b><i class="iconfont icon-dui icon"></i>/<br />
        <b></b><i class="iconfont icon-ai10 icon"></i><br />
        <b></b><i class="iconfont icon-ai09 icon"></i><br />
        /
      </p>
      <h3 class="section-title"></h3>
      <p>
        <b>SPACE</b><i class="bf-icon bf-icon-play icon"></i><i class="bf-icon bf-icon-pause icon"></i>/<br />
        <b>A</b><i class="bf-icon bf-icon-xiazai icon"></i><br />
        <b>S</b><i class="bf-icon bf-icon-shangchuan icon"></i><br />
        <b></b>5<br />
        <b></b>5<br />
        <b>W</b><i class="bf-icon bf-icon-mini icon"></i><br />
        <b>F</b><i class="bf-icon bf-icon-fullscreen icon"></i><b></b><br />
        <b></b><i class="bf-icon bf-icon-previous icon"></i><br />
        <b></b><i class="bf-icon bf-icon-next icon"></i><br />
        <b></b><i class="bf-icon bf-icon-save icon"></i><br />
        <b></b><i class="bf-icon bf-icon-wancheng icon"></i>/<br />
        <b></b>
      </p>
    </div>
  </div>
</template>

<script>
import { getCount } from "@/models/Task";
export default {
  name: "Home",
  data() {
    return {
      account: this.global.account,
      userCount: 0,
      reviewerCount: 0,
      taskCount: 0,
      loading: false
    };
  },
  mounted() {
    if (this.account.group == 3) {
      this.loading = true;
      getCount(this.account)
        .then(data => {
          this.userCount = data.data.userCount;
          this.reviewerCount = data.data.reviewerCount;
          this.taskCount = data.data.taskCount;
        })
        .catch(data => {
          this.$message({ type: "error", message: "" });
        })
        .then(() => {
          this.loading = false;
        });
    }
  }
};
</script>

<style scoped>
.dashboard {
  display: flex;
  flex-wrap: wrap;
}
.dashboard-panel {
  width: 200px;
  height: 180px;
  text-align: center;
  margin: 10px;
  border-radius: 5px;
  position: relative;
}
.panel-number {
  padding: 15px 5px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.11);
  background-color: #f9f9f9;
  color: #696969;
}
.panel-header {
  font-size: 20px;
}
.panel-content {
  font-size: 50px;
  position: absolute;
  left: 0;
  right: 0;
  top: 80px;
}
.guide > p {
  line-height: 2;
}
.icon {
  background-color: #eeee;
  border-radius: 3px;
  padding: 5px;
}
</style>



