<template>
  <div>
    <el-button icon="el-icon-plus" @click="newTaskAdd" type="primary"></el-button>
    <el-button icon="el-icon-refresh-left" @click="showList"></el-button>
    <el-input
      v-model="search"
      @keydown.enter.native="searchClick"
      class="search"
      prefix-icon="el-icon-search"
      placeholder=""
    ></el-input>

    <el-table border :data="taskData.list" stripe style="width: 100%;margin-top:10px;" v-loading="loading">
      <el-table-column
        :filters="[
          { text: '', value: '1' },
          { text: '', value: '2' },
          { text: '', value: '3' },
          { text: '', value: '4' },
          { text: '', value: '5' }
        ]"
        :filter-method="filterTag"
        filter-placement="bottom-end"
        prop="authorities"
        label=""
        width="180"
      >
        <template slot-scope="scope">
          {{ getTypeName(scope.row.taskType) }}
        </template>
      </el-table-column>
      <el-table-column prop="taskName" min-width="100" label=""></el-table-column>
      <el-table-column fixed="right" label="" width="530">
        <template slot-scope="scope">
          <el-button v-if="scope.row.taskType != 5" @click="editType(scope.row)" size="mini"></el-button>
          <el-button @click="editUser(scope.row)" size="mini"></el-button>
          <el-button @click="editReviewer(scope.row)" size="mini"></el-button>
          <el-button @click="editLabel(scope.row)" size="mini"></el-button>
          <el-button @click="splitTask(scope.row)" size="mini"></el-button>
          <el-button @click="deleteTask(scope.row)" type="danger" size="mini"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!---->
    <el-dialog
      :title="userSelect.type == 'user' ? '' : ''"
      :visible.sync="userSelect.dialogVisible"
      @open="openUserDialog"
      width="400px"
    >
      <el-table ref="table" :data="userSelect.tableData" style="width: 100%" @selection-change="handleSelectionChange" v-loading="userSelect.loading">
        <el-table-column prop="join" type="selection" width="55"></el-table-column>
        <el-table-column label="" prop="username"></el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button @click="userSelect.dialogVisible = false"> </el-button>
        <el-button type="primary" @click="setUserTable"> </el-button>
      </span>
    </el-dialog>
    <!---->
    <el-dialog title="" :visible.sync="labelSelect.dialogVisible" @open="openSelectDialog" width="400px">
      <el-table
        ref="labelTable"
        :data="labelSelect.tableData"
        style="width: 100%"
        @selection-change="labelSelectionChange"
        v-loading="labelSelect.loading"
      >
        <el-table-column prop="join" type="selection" width="55"></el-table-column>
        <template v-if="labelSelect.taskItem.taskType != 5">
          <el-table-column label="" prop="labelName">
            <template slot-scope="scope">
              <el-tag v-if="labelSelect.index[scope.row.labelName]" class="task-tag" effect="plain" size="mini">{{
                labelSelect.index[scope.row.labelName]
              }}</el-tag
              >{{ scope.row.labelName }}
            </template>
          </el-table-column>
        </template>
        <template v-else>
          <el-table-column label="" prop="type">
            <template slot-scope="scope">
              <span>{{ scope.row.type == 0 ? "" : scope.row.type == 1 ? "" : "" }}</span>
            </template>
          </el-table-column>
          <el-table-column label="" prop="question"></el-table-column>
        </template>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button @click="labelSelect.dialogVisible = false"> </el-button>
        <el-button type="primary" @click="setLabelTable"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="split.dialogVisible" width="30%" v-loading="loading">
      <el-form :inline="true">
        <el-form-item label="">
          <el-input-number :min="2" :max="split.item.imageNumber" v-model="split.count" :step="1" step-strictly></el-input-number>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="split.dialogVisible = false"> </el-button>
        <el-button type="primary" @click="splitSubmit"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="typeSelect.dialogVisible" width="30%" v-loading="typeSelect.loading">
      <el-form :inline="true">
        <el-form-item label="" prop="group">
          <el-select v-model="typeSelect.select" placeholder="">
            <el-option v-for="item in newTask.type" :key="item.value" :label="item.label" :value="item.value"> </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="typeSelect.dialogVisible = false"> </el-button>
        <el-button type="primary" @click="typeSubmit"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="newTask.dialogVisible" width="60%" v-loading="newTask.loading">
      <el-table border :data="newTask.list" stripe style="width: 100%;margin-top:10px;" v-loading="loading">
        <el-table-column prop="taskName" min-width="100" label=""></el-table-column>
        <el-table-column label="" width="120">
          <template slot-scope="scope">
            <el-select v-if="scope.row.taskType != 5" v-model="scope.row.taskType" placeholder="">
              <el-option v-for="item in newTask.type" :key="item.value" :label="item.label" :value="item.value"> </el-option>
            </el-select>
            <span v-else></span>
          </template>
        </el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="newTaskAddSubmit"></el-button>
      </span>
    </el-dialog>
    <el-pagination
      v-if="page.total > 1"
      @current-change="pageChange"
      class="pagination"
      background
      layout="prev, pager, next,jumper"
      :page-count="page.total"
      :current-page.sync="page.now"
    ></el-pagination>
  </div>
</template>
<script>
import Partition from "@/models/Partition";
import Video from "@/models/Video";
export default {
  name: "partition",
  data() {
    return {
      taskData: this.global.task,
      userSelect: {
        dialogVisible: false,
        tableData: [],
        tableSelect: [],
        type: "user",
        taskItem: [],
        loading: false
      },
      labelSelect: {
        dialogVisible: false,
        tableData: [],
        tableSelect: [],
        index: [], //tableDataarr,index
        taskItem: [],
        loading: false
      },
      typeSelect: {
        dialogVisible: false,
        select: "",
        taskItem: null,
        loading: false
      },
      newTask: {
        list: [],
        dialogVisible: false,
        loading: false,
        type: [{ value: 1, label: "" }, { value: 2, label: "" }, { value: 3, label: "" }, { value: 4, label: "" }]
      },
      account: this.global.account,
      loading: false,
      split: {
        dialogVisible: false,
        count: 2,
        item: ""
      },
      page: { now: 1, total: 1 },
      search: ""
    };
  },
  methods: {
    newTaskAdd() {
      this.newTask.dialogVisible = true;
      this.newTask.loading = true;
      Partition.getNewTaskList()
        .then(data => {
          this.newTask.list = data.data.taskList;
          this.newTask.loading = false;
        })
        .catch(data => {
          this.$message({ type: "error", message: `,${data.message}(${data.code})` });
        })
        .then(() => {
          this.newTask.loading = false;
        });
    },
    newTaskAddSubmit() {
      this.newTask.loading = true;
      Partition.updateTaskType(this.newTask.list)
        .then(() => {
          this.$message({ type: "success", message: `` });
          this.typeSelect.dialogVisible = false;
          this.showList();
        })
        .catch(data => {
          this.$message({ type: "error", message: `,${data.message}(${data.code})` });
        })
        .then(() => {
          this.newTask.loading = false;
        });
    },
    typeSubmit() {
      this.typeSelect.loading = true;
      Partition.updateTaskType([{ taskId: this.typeSelect.taskItem.taskId, taskType: this.typeSelect.select }])
        .then(() => {
          this.$message({ type: "success", message: `` });
          this.typeSelect.dialogVisible = false;
          this.showList();
        })
        .catch(data => {
          this.$message({ type: "error", message: `,${data.message}(${data.code})` });
        })
        .then(() => {
          this.typeSelect.loading = false;
        });
    },
    showList() {
      this.loading = true;

      Partition.getTaskList(this.page.now)
        .then(data => {
          this.page.total = data.data.totalpages ? data.data.totalpages : 1;
          this.global.task.list = data.data.taskList;
        })
        .catch(data => {
          this.$message({ type: "error", message: `,${data.message}(${data.code})` });
        })
        .then(() => {
          this.loading = false;
        });
    },
    searchClick() {
      this.page.now = 1;
      if (this.search == "") {
        this.showList();
        return;
      }
      this.loading = true;
      Partition.search(this.search)
        .then(data => {
          this.global.task.list = data.data;
        })
        .catch(data => {
          this.$message({ type: "error", message: `,${data.message}(${data.code})` });
        })
        .then(() => {
          this.loading = false;
        });
    },
    editUser(item) {
      [this.userSelect.dialogVisible, this.userSelect.type, this.userSelect.taskItem] = [true, "user", item];
    },
    editReviewer(item) {
      [this.userSelect.dialogVisible, this.userSelect.type, this.userSelect.taskItem] = [true, "reviewer", item];
    },
    editLabel(item) {
      [this.labelSelect.dialogVisible, this.labelSelect.taskItem] = [true, item];
    },
    editType(item) {
      [this.typeSelect.dialogVisible, this.typeSelect.taskItem, this.typeSelect.select] = [true, item, item.taskType];
    },
    setUserTable() {
      this.loading = true;
      this.userSelect.dialogVisible = false;
      let ps =
        this.userSelect.type == "user"
          ? Partition.setUser(this.userSelect.taskItem, this.userSelect.tableSelect, this.account)
          : Partition.setReviewer(this.userSelect.taskItem, this.userSelect.tableSelect, this.account);
      ps.then(() => {
        this.$message({ type: "success", message: `` });
      })
        .catch(data => {
          this.$message({ type: "error", message: `,${data.message}(${data.code})` });
        })
        .then(() => {
          this.loading = false;
          this.showList();
        });
    },
    openUserDialog() {
      this.$nextTick(() => {
        this.userSelect.loading = true;
        let ps = Partition.getList(this.userSelect.taskItem, this.userSelect.type == "reviewer" ? "Reviewer" : "User");
        ps.then(data => {
          this.userSelect.tableData = data;
          this.$nextTick(() => {
            this.userSelect.tableSelect = [];
            for (const item of data) {
              if (item.join) {
                this.$refs.table.toggleRowSelection(item, true);
              } else {
                this.$refs.table.toggleRowSelection(item, false);
              }
            }
          });
        })
          .catch(data => {
            this.$message({ type: "error", message: `,${data.message}(${data.code})` });
          })
          .then(() => {
            this.userSelect.loading = false;
          });
      });
    },
    handleSelectionChange(val) {
      this.userSelect.tableSelect = val;
    },
    setLabelTable() {
      this.loading = true;
      this.labelSelect.dialogVisible = false;
      console.log(this.labelSelect.tableSelect);
      Partition.setLabel(this.labelSelect.taskItem, this.labelSelect.tableSelect, this.account)
        .then(() => {
          this.$message({ type: "success", message: `` });
        })
        .catch(data => {
          this.$message({ type: "error", message: `,${data.message}(${data.code})` });
        })
        .then(() => {
          this.loading = false;
          this.showList();
        });
    },
    labelSelectionChange(val) {
      if (this.labelSelect.taskItem.taskType == 5) {
        if (val.length > 1) {
          this.$message({ type: "error", message: `` });
          this.$refs.labelTable.toggleRowSelection(this.labelSelect.tableSelect[0]);
          this.labelSelect.tableSelect = [this.labelSelect.tableSelect[this.labelSelect.tableSelect.length - 1]];
          return false;
        }
      }
      this.labelSelect.index = [];
      if (this.labelSelect.taskItem.taskType == 4) {
        let i = 1;
        for (const item of val) {
          // console.log(item.labelName);
          this.labelSelect.index[item.labelName] = i++;
        }
      }
      this.labelSelect.tableSelect = val;
      // console.log("select :", val);
      // console.log("index :",  this.labelSelect.index );
    },
    openSelectDialog() {
      this.$nextTick(() => {
        this.labelSelect.loading = true;
        let ps = Partition.getLabelList(this.labelSelect.taskItem.taskType, this.labelSelect.taskItem.labelIds);
        ps.then(data => {
          this.labelSelect.tableData = data;
          this.$nextTick(() => {
            this.labelSelect.tableSelect = [];
            for (const item of data) {
              if (item.join) {
                this.$refs.labelTable.toggleRowSelection(item, true);
              } else {
                this.$refs.labelTable.toggleRowSelection(item, false);
              }
            }
          });
        })
          .catch(data => {
            this.$message({ type: "error", message: `,${data.message}(${data.code})` });
          })
          .then(() => {
            this.labelSelect.loading = false;
          });
      });
    },
    splitTask(item) {
      if (item.imageNumber <= 1) {
        this.$alert("", "", { confirmButtonText: "" });
        return;
      }
      this.split.item = item;
      this.split.dialogVisible = true;
    },
    splitSubmit() {
      this.loading = true;
      Partition.splitTask(this.split.item.taskId, this.split.count, this.account)
        .then(() => {
          this.$message({ type: "success", message: `` });
        })
        .catch(data => {
          this.$message({ type: "error", message: `,${data.message}(${data.code})` });
        })
        .then(() => {
          this.split.dialogVisible = false;
          this.showList();
        });
    },
    deleteTask(item) {
      this.$confirm(", ?", "", { confirmButtonText: "", cancelButtonText: "", type: "warning" }).then(() => {
        this.loading = true;
        Partition.deleteTask(item.taskId, this.account)
          .then(() => {
            this.$message({ type: "success", message: `` });
          })
          .catch(data => {
            this.$message({ type: "error", message: `,${data.message}(${data.code})` });
          })
          .then(() => {
            this.loading = false;
            this.showList();
          });
      });
    },
    getTypeName(type) {
      switch (type) {
        case 1:
          return "";
        case 2:
          return "";
        case 3:
          return "";
        case 4:
          return "";
        case 5:
          return "";
        default:
          return "";
      }
    },
    filterTag(val, row) {
      return row.taskType == val;
    },
    pageChange(p) {
      this.showList();
    }
  },
  mounted() {
    this.showList();
  }
};
</script>


