<template>
  <div>
    <el-button icon="el-icon-plus" @click="addLabelBtn" type="primary"></el-button>
    <el-button icon="el-icon-refresh-left" @click="showList"></el-button>
    <el-input v-model="search" class="search" prefix-icon="el-icon-search" placeholder=""></el-input>
    <el-table border :data="labelData" stripe style="width: 100%;margin-top:10px;" v-loading="loading">
      <el-table-column prop="type" label="">
        <template slot-scope="scope">
          <span>{{ scope.row.type == 0 ? "" : scope.row.type == 1 ? "" : "" }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="question" min-width="100" label=""></el-table-column>
      <el-table-column prop="selector" label="">
        <template slot-scope="scope">
          <span>{{ scope.row.selector == null ? "" : parserSelector(scope.row.selector) }}</span>
        </template>
      </el-table-column> 
      <el-table-column fixed="right" label="" width="160">
        <template slot-scope="scope">
          <el-button v-if="scope.row.type != 0" @click="editLabel(scope.row)" size="mini"></el-button>
          <el-button v-if="scope.row.type != 0" @click="deleteLabel(scope.row)" type="danger" size="mini"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog v-loading="loading" title="/" :visible.sync="dialogVisible" width="30%">
      <el-form ref="form" :rules="rules" :hide-required-asterisk="true" :model="form" label-width="80px">
        <el-form-item label="" prop="type">
          <el-select v-model="form.type" placeholder="">
            <el-option label="" :value="1"></el-option>
            <el-option label="" :value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="question">
          <el-input placeholder="'$'" v-model="form.question"></el-input>
        </el-form-item>
        <el-form-item v-if="form.type == 2" label="" prop="selector">
          <el-input placeholder="','" v-model="form.selector"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="addLabel"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import Video from "@/models/Video.js";
export default {
  name: "Labelset",
  data() {
    return {
      form: {
        question: "",
        type: 1,
        selector: ""
      },
      rules: {
        question: [{ required: true, message: "", trigger: "change" }],
        type: [{ required: true, message: "", trigger: "blur" }],
        selector: [{ required: true, message: "", trigger: "change" }]
      },
      labelData: [],
      dialogVisible: false,
      editId: 0,
      type: "add",
      loading: false,
      account: this.global.account,
      search: "",
      tempLabelData: []
    };
  },
  watch: {
    search() {
      let v = this.search.toLowerCase();
      if (v == "") {
        this.labelData = this.tempLabelData;
        return;
      }
      let temp = this.tempLabelData.filter(item => item.question != null && item.question.toLowerCase().indexOf(v) != -1);
      this.labelData = temp;
    }
  },
  methods: {
    showList() {
      this.search = "";
      this.loading = true;
      Video.getLabelList()
        .then(data => {
          this.labelData = data.data;
          this.tempLabelData = data.data;
        })
        .catch(data => {
          this.$message({ type: "error", message: `,${data.message}(${data.code})` });
        })
        .then(() => {
          this.loading = false;
        });
    },
    addLabelBtn() {
      this.type = "add";
      this.dialogVisible = true;
      this.form = {
        question: "",
        type: 1,
        selector: ""
      };
    },
    editLabel(item) {
      this.type = "edit";
      this.editId = item.labelId;
      this.form.question = item.question;
      this.form.type = item.type;
      this.form.selector = this.parserSelector(item.selector);
      this.dialogVisible = true;
    },
    deleteLabel(item) {
      this.loading = true;
      Video.deleteLabel(item.labelId, this.account)
        .then(() => {
          this.$message({ type: "success", message: `"${item.question}"` });
        })
        .catch(data => {
          this.$message({ type: "error", message: `"${item.question}",${data.message}(${data.code})` });
        })
        .then(() => {
          this.loading = false;
        });
      this.showList();
    },
    addLabel() {
      this.$refs.form.validate(valid => {
        if (!valid) {
          return false;
        }
        this.loading = true;
        let ps =
          this.type == "add"
            ? Video.addLabel(this.form.type, this.form.question, this.parserSelector(this.form.selector, true))
            : Video.editLabel(this.editId, this.form.type, this.form.question, this.parserSelector(this.form.selector, true));
        ps.then(() => {
          this.$message({ type: "success", message: `${this.type == "add" ? "" : ""}` });
        })
          .catch(data => {
            this.$message({ type: "error", message: `${this.type == "add" ? "" : ""},${data.message}(${data.code})` });
          })
          .then(() => {
            this.dialogVisible = false;
            this.loading = false;
            this.showList();
          });
      });
    },
    parserSelector(source, toArray) {
      if (toArray) {
        return source.split(",");
      }
      console.log(source);
      if (source == "") {
        return null;
      }
      let t = "";
      for (let i = 0; i < source.length; i++) {
        t += source[i] + (i != source.length - 1 ? "," : "");
      }
      return t;
    }
  },
  mounted() {
    this.showList();
  }
};
</script>
<style scoped>
.label-color {
  width: 10px;
  height: 10px;
  display: inline-block;
  border-radius: 10px;
}

.search {
  width: 200px;
  margin: 0 10px;
}
</style>



