 <template>
  <div style="width:100%;height:100%;" v-loading="loading">
    <el-page-header class="page-header" @back="goBack" content=""></el-page-header>
    <div class="label-core" tabindex="1">
      <div class="label-toolbar">
        <ul>
          <a class="select" title="(A)" href="javascript:void(0)">
            <li class="active">
              <span class="iconfont icon-zhizhen"></span>
            </li>
          </a>
          <a class="move" title="/(W)" href="javascript:void(0)">
            <li>
              <span class="iconfont icon-yidong"></span>
            </li>
          </a>
          <!--
          <a
            :style="{ display: type == 3 || (type == 4 && this.labelIndex != this.label.length) ? 'block' : 'none' }"
            class="polygon"
            title="(Q)"
            href="javascript:void(0)"
          >
            <li>
              <span class="iconfont icon-duobianxing"></span>
            </li>
          </a>
          -->
          <a
            :style="{ display: type==2 || type == 3 || (type == 4 && this.labelIndex != this.label.length) ? 'block' : 'none' }"
            class="polygon"
            title="(Q)"
            href="javascript:void(0)"
          >
            <li>
              <span class="iconfont icon-duobianxing"></span>
            </li>
          </a>

          <a
            :style="{ display: type == 2 || (type == 4 && this.labelIndex != this.label.length) ? 'block' : 'none' }"
            class="rectangle"
            title="(R)"
            href="javascript:void(0)"
          >
            <li>
              <span class="iconfont icon-kuangxuanquyu"></span>
            </li>
          </a>
          <div class="label-divider"></div>
          <a :style="{ display: type != 1 && type != 4 ? 'block' : 'none' }" class="edit" title="(E)" href="javascript:void(0)">
            <li>
              <span class="iconfont icon-sheji"></span>
            </li>
          </a>
          <a @click="classifyClick" v-if="type == 1" class="classify" title="" href="javascript:void(0)">
            <li>
              <span class="iconfont icon-sheji"></span>
            </li>
          </a>
          <a @click="nextLabel" v-if="type == 4" class="classify" title="" href="javascript:void(0)">
            <li>
              <span class="iconfont icon-nextweek"></span>
            </li>
          </a>
          <a :style="{ display: type != 1 ? 'block' : 'none' }" class="delete" title="(D)" href="javascript:void(0)">
            <li>
              <span class="iconfont icon-shanchu"></span>
            </li>
          </a>
          <div class="label-divider"></div>
          <a class="zoomin" title="(Z)" href="javascript:void(0)">
            <li>
              <span class="iconfont icon-fangda"></span>
            </li>
          </a>
          <a class="zoomout" title="(X)" href="javascript:void(0)">
            <li>
              <span class="iconfont icon-suoxiao"></span>
            </li>
          </a>

          <div class="label-divider"></div>
          <a
            :style="{ display: account.group == 3 ? 'block' : 'none' }"
            @click="showDelete"
            class="delete" title="" href="javascript:void(0)" >
            <li>
              <span class="el-icon-circle-close"></span>
            </li>
          </a>
          <a class="save" title="(Ctrl+S)" href="javascript:void(0)">
            <li>
              <span class="iconfont icon-baocun"></span>
            </li>
          </a>
          <a
            :style="{ display: account.group == 2 || account.group == 3 ? 'block' : 'none' }"
            class="confirm"
            @click="showComfirm"
            title="(Ctrl+C)"
            href="javascript:void(0)"
          >
            <li>
              <span class="iconfont icon-dui"></span>
            </li>
          </a>
          <div class="label-divider"></div>
          <a @click="previous" class="zoomin" title="" href="javascript:void(0)">
            <li>
              <span class="iconfont icon-ai10"></span>
            </li>
          </a>
          <a @click="next" class="zoomout" title="" href="javascript:void(0)">
            <li>
              <span class="iconfont icon-ai09"></span>
            </li>
          </a>
        </ul>
      </div>
      <div class="label-area">
        <div class="label-main">
          <img v-on:load="loadMark" :src="img" alt srcset />

          <canvas></canvas>
          <svg class="label-svg" xmlns="http://www.w3.org/2000/svg" version="1.1" />

        </div>
      </div>
      <div class="label-popup" style="display: none;">
        <select class="label-label"></select>
        <div class="label-checkbox">
          <input type="checkbox" class="label-crowd" value="iscrowd" />
          <label for="iscrowd">RLE</label>
        </div>
        <textarea class="label-desc" rows="5" placeholder=""></textarea>
        <button class="label-save-label" type="button"></button>
        <button class="label-delete-label" type="button"></button>
      </div>

      <div class="label-list">

        <el-collapse :style="{ display: account.group != 1 ? 'block' : 'none' }" v-model="penddingUserActive" class="label-user">
          <el-collapse-item class="label-user-item" title="" name="user">
            <el-menu @select="penddingUserSelect" :default-active="activeIndex" class="el-menu-vertical-demo">
              <el-menu-item v-for="item in penddingUserList" :key="item.userId" :index="item.userId + ''">
                <span slot="title">{{ item.username }}</span>
              </el-menu-item>
            </el-menu>
          </el-collapse-item>
        </el-collapse>

        <ul class="label-list-ul" ></ul>


        <el-card  body-style="{ padding: '30px'}" shadow="always" class="box-card">
          <div v-if="task.list[task.index].userConfirmId != null" class="yishenghe">
            <h3></h3>
          </div>
          <div v-else class="weishenghe">
            <h3></h3>
          </div>
        </el-card>




      </div>

    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="classify.dialogVisible" width="30%">
      <el-form :inline="true">
        <el-form-item label="" prop="group">
          <el-select v-model="classify.select" multiple collapse-tags style="margin-left: 20px;" placeholder="">
            <el-option v-for="item in classify.label" :key="item.value" :label="item.label" :value="item.value"> </el-option>
          </el-select>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="classify.dialogVisible = false"> </el-button>
        <el-button type="primary" @click="classifyAdd"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { getImg, getPendingUserList, setFinalVersion, saveLabel, deleteImageById} from "@/models/Task.js";
import { LabelCore } from "@/assets/js/label.core.js";
import { HOST as Host, ajaxImg } from "@/models/Service.js";
import { getImgList } from "@/models/Task.js";
export default {
  name: "label",
  props: ["id", "tid", "type", "taskName", "imagePage"],
  data() {
    return {
      img: "",
      label: [],
      name: "",
      area: [],
      count: 0,
      showComfirmed: true,
      showDeleted:false,
      penddingUserList: [],
      isReviewer: false,
      loading: true,
      penddingUserSelectId: 0,
      labelCore: undefined,
      penddingUserActive: "user",
      account: this.global.account,
      activeIndex: "",
      task: {
        page: +this.imagePage,
        total: 0,
        id: this.tid,
        list: [],
        index: 0,
        name: this.taskName
      },
      classify: {
        label: [],
        select: [],
        dialogVisible: false
      },
      labelIndex: 0
    };
  },
  watch: {
    id() {
      this.load(true);
    }
  },
  methods: {
    showDelete(){
      console.log("delete");
      this.loadMark();
      this.$confirm("", "", {confirmButtonText: "", cancelButtonText: "", type: "info"})
        .then(() => {
            this.deleteImageById();
          // this.setFinalVersion();
        })
        .catch(() => {
        });

    },
    showComfirm(){
      console.log("func");
      this.$confirm("", "", {confirmButtonText: "", cancelButtonText: "", type: "info"})
        .then(() => {
          this.setFinalVersion();
          // this.setFinalVersion();
        })
        .catch(() => {
        });
    },
    format(percentage) {
      return percentage === 100 ? '' : `${percentage}%`;
    },
    classifyClick() {
      this.classify.label = [];
      for (const item of this.label) {
        this.classify.label.push({ label: item.label, value: item.id });
      }
      this.classify.dialogVisible = true;
    },
    classifyAdd() {
      this.labelCore.clean();
      for (const id of this.classify.select) {
        this.labelCore.setClassify(id);
      }
      this.classify.dialogVisible = false;
    },
    load(hasCom) {
      if (this.account.group == 1) {
        this.getImgInfo(hasCom);
      } else if (this.account.group == 3 || this.account.group == 2) {
        if (hasCom) {
          this.getImgInfo(hasCom);
        } else {
          this.getUserLabelList(hasCom);
        }
      } else {
        this.$message({ type: "error", message: `` });
      }
      if (this.account.group != 0 && !hasCom) {
        this.getImageList()
          .then()
          .catch();
      }
    },
    //
    getImgInfo(isReload) {
      this.loading = true;
      getImg(this.id, this.account.group == 1 ? this.account.id : this.penddingUserSelectId, this.account)
        .then(data => {
          let src = Host + `/api/image/${this.taskName}/${data.data.image.imageName}`;
          if (this.img == src) {
            this.loading = false;
          }
          let str = JSON.stringify(data.data.labels);
          str = str
            .replace(/labelId/g, "id")
            .replace(/labelName/g, "label")
            .replace(/labelType/g, "type")
            .replace(/labelColor/g, "color");
          this.label = JSON.parse(str);
          this.name = data.data.image.imageName;
          this.area = [];
          this.classify.select = [];
          this.labelIndex = 0;
          let templabel = "";
          for (const item of data.data.datas) {
            let points = [];

            for (const point of item.point) {
              points.push([point.x, point.y]);
            }
            this.area.push({
              labelId: item.labelId,
              labelType: item.labelType,
              desc: item.dataDesc,
              points: points,
              iscrowd: item.iscrowd
            });
            if (this.type == 1) {
              this.classify.select.push(item.labelId);
            }
            if (templabel != item.labelId) {
              this.labelIndex++;
              templabel = item.labelId;
            }
          }
          if (this.img == src) {
            this.labelCore.reload(this.area);
            this.loadLabelIndex();
            return;
          }
          this.img = src;
        })
        .catch(data => {
          this.loading = false;
          this.$alert(`${data.message}(${data.code})\r\n`, "", { confirmButtonText: "", type: "warning" });
        });
    },
    //
    getUserLabelList(hasCom) {
      getPendingUserList(this.id, this.account)
        .then(d => {
          let data = d.data;
          if (data.length > 0) {
            this.activeIndex = data[0].userId + "";
            this.penddingUserSelectId = data[0].userId;
            this.getImgInfo(hasCom);
            this.penddingUserList = data;
          } else {
            //prettier-ignore
            this.$alert("", "", {confirmButtonText: "",callback: action => {this.$router.go(-1);}});
          }
        })
        .catch(data => {
          this.$message({ type: "error", message: `${data.message}(${data.code})` });
        });
    },
    //(label.core)
    loadMark() {
      if (this.img == "") {
        return;
      }
      this.loading = false;
      if (this.labelCore != null) {
        this.labelCore.reload(this.area);

        this.loadLabelIndex();
        return;
      }
      this.labelCore = new LabelCore(
        document.querySelector(".label-core"),
        this.label,
        this.area,
        data => {
          this.saveLabelArea(data);
        },
        data => {
          // this.$confirm("", "", {confirmButtonText: "", cancelButtonText: "", type: "info"})
          //   .then(() => {
          //     this.setFinalVersion();
          //     // this.setFinalVersion();
          //   })
          //   .catch(() => {
          //   });
        },
        //addCB
        () => {
        },
        //removeCB
        () => {
          if (this.type == 4) {
            this.labelCore.setLabelIndex(--this.labelIndex);
            this.$message({ type: "info", message: `:'${this.label[this.labelIndex].label}'` });
          }
        }
      );
      this.loadLabelIndex();
    },
    loadLabelIndex() {
      if (this.type == 4) {
        console.log(this.labelIndex);
        this.labelCore.setLabelIndex(this.labelIndex);
        if (this.labelIndex == this.label.length) {
          this.$message({ type: "success", message: `` });
          return;
        }
        this.$message({ type: "info", message: `:'${this.label[this.labelIndex].label}'` });
      }
    },
    nextLabel() {
      this.labelCore.setLabelIndex(++this.labelIndex);
      if (this.labelIndex >= this.label.length) {
        this.labelIndex = this.label.length;
        this.$message({ type: "success", message: `` });
        return;
      }
      this.$message({ type: "info", message: `:'${this.label[this.labelIndex].label}'` });
    },
    //
    goBack() {
      this.$confirm("", "", { confirmButtonText: "", cancelButtonText: "", type: "warning" })
        .then(() => {
          this.$router.push(`/task/${this.tid}/${this.taskName}/${this.type}`);
        })
        .catch(() => {});
    },
    //event
    penddingUserSelect(index) {
      this.penddingUserSelectId = parseInt(index);
      this.getImgInfo(true);
    },
    //
    setFinalVersion() {
      this.loading = true;
      setFinalVersion(parseInt(this.id), this.penddingUserSelectId, this.account)
        .then(() => {
          this.$message({ type: "success", message: "" });
        })
        .catch(data => {
          this.$message({ type: "error", message: `${data.message}(${data.code})` });
        })
        .then(() => {
          this.loading = false;
        });
    },
    deleteImageById(){
      this.loading = true;
      deleteImageById(parseInt(this.id), this.penddingUserSelectId, this.account)
        .then(() => {
          this.$message({ type: "success", message: "" });
          this.next();
        })
        .catch(data => {
          this.$message({ type: "error", message: `${data.message}(${data.code})` });
        })
        .then(() => {
          this.loading = false;
        });

    },
    //
    saveLabelArea(data) {
      this.loading = true;
      let postData = [];
      for (const item of data) {
        let points = [],
          i = 0;
        for (const point of item.points) {
          points.push({
            x: point[0],
            y: point[1],
            order: ++i
          });
        }
        postData.push({
          labelId: item.id,
          labelType: item.type,
          dataDesc: item.desc,
          iscrowd: item.iscrowd,
          point: points
        });
      }
      let ps = saveLabel(this.id, postData, this.account.group == 1 ? this.account.id : this.penddingUserSelectId, this.account);
      ps.then(() => {
        this.$message({ type: "success", message: "" });
      })
        .catch(data => {
          this.$message({ type: "error", message: `${data.message}(${data.code})` });
        })
        .then(() => {
          this.loading = false;
        });
    },
    //
    getImageList(isNew) {
      return new Promise((resolve, reject) => {

        console.log(this.task.page);
        getImgList(this.task.id, this.task.page)
          .then(data => {
            let list = data.data.images;
            this.task.list = list;
            this.task.total = data.data.totalpages;
            if (!isNew) {
              for (let i = 0; i < list.length; i++) {
                if (list[i].imageId == this.id) {
                  this.task.index = i;
                }
              }
            }
            resolve();
          })
          .catch(() => {
            this.$message({ type: "error", message: `` });
            reject();
          });
      });
    },
    next() {
      let task = this.task;
      if (task.list.length - 1 == task.index) {
        console.log("");
        if (task.page == task.total) {
          this.$message({ type: "warning", message: `` });
          return;
        }
        console.log(task.page);
        task.page++;
        console.log(task.page);
        task.index = 0 ;
        console.log(task.list[task.index].imageId);
        getImgList(this.task.id, this.task.page)
          .then(data => {
            let list = data.data.images;
            this.task.list = list;
            console.log("success");
            console.log(task.list[task.index].imageId);
            console.log(task.list[task.index].userConfirmId);
            this.$router.push(`/img/${task.list[task.index].imageId}/${task.id}/${this.type}/${task.name}/${task.page}`);
          });
        console.log(task.list[task.index].imageId);
        console.log(task.list);
        //this.$router.push(`/img/${task.list[task.index].imageId}/${task.id}/${this.type}/${task.name}/${task.page}`);
      } else {
        console.log(task.list);
        ++task.index;
        console.log(task.list[task.index].imageId);
        this.$router.push(`/img/${task.list[task.index].imageId}/${task.id}/${this.type}/${task.name}/${task.page}`);
      }
    },
    previous() {
      let task = this.task;
      if (task.index == 0) {
        if (task.page == 1) {
          this.$message({ type: "warning", message: `` });
          return;
        }
        console.log(task.page);
        task.page--;
        console.log(task.page);

        getImgList(this.task.id, this.task.page)
          .then(data => {
            let list = data.data.images;
            this.task.list = list;
            task.index = task.list.length - 1;
            console.log("success");
            console.log(task.list[task.index].imageId);
            this.$router.push(`/img/${task.list[task.index].imageId}/${task.id}/${this.type}/${task.name}/${task.page}`);
          });

      } else {
        --task.index;
        this.$router.push(`/img/${task.list[task.index].imageId}/${task.id}/${this.type}/${task.name}/${task.page}`);
      }
    }
  },
  mounted() {
    this.load();
  }
};
</script>
<style src="@/assets/css/label.core.css"></style>







